import React from 'react';
import {gql, useMutation, useQuery} from "@apollo/client";
import {GET_CATEGORIES} from "../store/Queries";
import {ADD_CATEGORY, DEL_CATEGORY} from "../store/Mutation";
import {showError} from "./ShowError";

const useCategories = () => {
    const {data,error:getError} = useQuery(GET_CATEGORIES)

    const [delCategoryMut,{error:delError}] = useMutation(DEL_CATEGORY, {
        update(cache, {data: {delCategory}}) {
            if (delCategory) {
                cache.modify({
                    fields: {
                        categories(existing = [], {readField}) {
                            return existing.filter(
                                categoryRef => delCategory !== readField('id', categoryRef),
                            );
                        }
                    }
                })
            }
        },errorPolicy:'none'
    })

    const [addCategoryMut, {error:addError}] = useMutation(ADD_CATEGORY, {
        update(cache, {data: {addCategory}}) {
            cache.modify({
                fields: {
                    categories(existing = []) {
                        if (addCategory) {
                            const newMediaFragment = cache.writeFragment({
                                data: addCategory,
                                fragment: gql`
                                    fragment NewProvider on ChannelGroupEntry {
                                        id
                                    }
                                `
                            });

                            return [newMediaFragment,...existing];
                        } else {
                            return [...existing]
                        }
                    }
                }
            })
        }
    })


    React.useEffect(() => {

        if (getError?.message) {
            showError(getError.message)
        }
        if (delError?.message) {
            showError(delError.message)
        }
        if (addError?.message) {
            showError(addError.message)
        }
    },[getError,delError,addError])



    return [data,addCategoryMut,delCategoryMut]
};

export default useCategories;
