import React from 'react';
import ProviderModal from "../modal/ProviderModal";
import SimpleHeader from "../component/SimpleHeader";
import {confirm} from "../util/ConfirmAlert";
import ProviderItem from "../items/ProviderItem";
import useProviders from "../util/useProviders";

const Providers = () => {

    const [data,addProviderMut,updateProviderMut,delProviderMut] = useProviders()


     const [handleModal, setHandleModal] = React.useState({isOpen: false, data: undefined, title: 'Add provider'})



    const handleEditModal = (provider) => setHandleModal({isOpen: true, data: provider, title: 'Edit provider'})

    const handleDelete = (id) => {
        confirm('Are you sure ?', () => delProviderMut({variables: {id: id}}))
    }

    const handleSave = (values) => {
        addProviderMut({variables: {model: values}})
            .then(_ => setHandleModal({isOpen: false, data: undefined}))
    }

    const handleEdit = (values) => {
        updateProviderMut({variables: {model: values}})
            .then(_ => setHandleModal({isOpen: false, data: undefined}))
    }


    return (
        <>
            <SimpleHeader buttonTitle='Add provider'
                          handleModal={() => setHandleModal({isOpen: true, data: undefined, title: 'Add provider'})}/>
            <div className="clearfix"/>


            <div className='content-wrapper'>
                <div className='container-fluid'>
                    {handleModal.isOpen &&
                        <ProviderModal data={handleModal.data} title={handleModal.title} show={handleModal.isOpen}
                                       handleClose={() => setHandleModal({isOpen: false, data: undefined})}
                                       handleSave={handleSave} handleEdit={handleEdit}/>
                    }

                    {/*<ProvidersList providers={data?.providers || []} delete={handleDelete}/>*/}
                    <div className={'list'} style={{maxHeight: 'calc(100vh - 100px)', overflowY: 'auto', overflowX:'hidden',paddingRight:10}}>
                    {data && data.providers.map((i, k) => (
                        <ProviderItem provider={i} key={k} handleDelete={handleDelete} handleEdit={handleEditModal}/>))}
                    </div>
                </div>
            </div>
        </>
    );
};

export default Providers;
