import React from 'react';
import {Col, Form, Modal, Row} from "react-bootstrap";
import {useFormik} from "formik";

const ProviderModal = ({show, handleClose, title, data, handleSave, handleEdit}) => {


    const formik = useFormik({
        initialValues: data ? data : {
            name: '',
            ua: '',
            x_forwarder: '',
            trimToLive: true,
            availabilityCheck: false,
            format: 'HLS',
            proxy:null,
            engine:'internal'
        },
        onSubmit: values => {
            if (data) {
                let edited = {
                    id: data.id,
                    name: values.name,
                    ua: values.ua,
                    x_forwarder: values.x_forwarder,
                    trimToLive: values.trimToLive,
                    availabilityCheck: values.availabilityCheck,
                    format: values.format,
                    proxy: values.proxy,
                    engine: values.engine
                }
                handleEdit(edited)
            } else {
                handleSave(values)
            }
        }
    })

    return (
        <Modal show={show} className={'fade'}>
            <div className="modal-content">
                <div className="modal-header">
                    <span className='bx bx-world me-2 text-success'/>
                    <h5 className="modal-title" id="exampleModalLabel1">{title}</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                            onClick={handleClose}/>

                </div>
                <div className="modal-body">

                    <form>
                        <div className="mb-3">
                            <label className="form-label" htmlFor="basic-default-name">Name</label>
                            <input type="text" name={'name'} className="form-control" id="basic-default-name"
                                   value={formik.values.name} onChange={formik.handleChange}/>
                        </div>
                        <div className="mb-3">
                            <label className="form-label" htmlFor="basic-default-ua">User agent</label>
                            <input type="text" name={'ua'} value={formik.values.ua} className="form-control"
                                   id="basic-default-ua"
                                   onChange={formik.handleChange}/>
                        </div>
                        <div className="mb-3">
                            <label className="form-label" htmlFor="basic-default-forwarder">X-Forwarded</label>
                            <input type="text" name={'x_forwarder'} value={formik.values.x_forwarder}
                                   className="form-control"
                                   id="basic-default-forwarder"
                                   onChange={formik.handleChange}/>
                        </div>
                        <div className="mb-3">
                            <label className="form-label" htmlFor="basic-default-forwarder">
                                <span className={'bx bx-sort'} />
                                Proxy</label>
                            <input type="text" name={'proxy'} value={formik.values.proxy}
                                   className="form-control"
                                   placeholder={'host:port'}
                                   id="basic-default-forwarder"
                                   onChange={formik.handleChange}/>
                        </div>
                        <Row className='mb-4'>
                            <Col>
                                <label className="form-label" htmlFor="basic-default-format">
                                    <span className={'bx bx-cog me-1'} />
                                    Engine
                                </label>
                                <Form.Select name="engine" className={'me-2'} id="basic-default-format"
                                             value={formik.values.engine}
                                             onChange={formik.handleChange}>
                                    <option key={1} value={'internal'}>Internal</option>
                                    <option key={2} value={'ffmpeg'}>FFmpeg</option>
                                </Form.Select>
                            </Col>
                            <Col>
                                <label className="form-label" htmlFor="basic-default-format">
                                    <span className={'bx bxs-playlist me-1'} />Output format
                                </label>
                                <Form.Select name="format" className={'me-2'} id="basic-default-format"
                                             value={formik.values.format}
                                             onChange={formik.handleChange}>
                                    <option key={1} value={'HLS'}>HLS</option>
                                    <option key={2} value={'MPEG-TS'}>MPEG-TS</option>
                                </Form.Select>
                            </Col>
                        </Row>

                        <Row className={formik.values.engine === 'internal' ? 'd-flex' : 'd-none'}>
                            <Col>
                                <div className="form-check">
                                    <input className="form-check-input" type="checkbox" name='trimToLive'
                                           onChange={formik.handleChange} id='trimToLive'
                                           checked={formik.values.trimToLive}/>
                                    <label className="form-check-label"
                                           htmlFor="trimToLive">Trim timeline to live</label>
                                </div>
                            </Col>
                            <Col>
                                <div className="form-check">
                                    <input className="form-check-input" type="checkbox" name='availabilityCheck'
                                           onChange={formik.handleChange} id='availabilityCheck'
                                           checked={formik.values.availabilityCheck}/>
                                    <label className="form-check-label"
                                           htmlFor="availabilityCheck">Segments availability checking</label>
                                </div>
                            </Col>
                        </Row>

                    </form>
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-outline-secondary" data-bs-dismiss="modal"
                            onClick={handleClose}>
                        Close
                    </button>
                    <button type="button" className="btn btn-primary" onClick={formik.handleSubmit}>Save changes
                    </button>
                </div>
            </div>

        </Modal>
    );
};

export default ProviderModal;
