import React from 'react';
import {ListGroup, Modal, Spinner} from "react-bootstrap";
import "react-datepicker/dist/react-datepicker.css";
import {useQuery} from "@apollo/client";
import {CHANNEL_INFO} from "../store/Queries";
import ReactCountryFlag from "react-country-flag";
import {langToCountryCode} from "../util/LangToCuntryCode";

const InfoModal = ({show, handleClose, title, id}) => {

    const {error, data} = useQuery(CHANNEL_INFO, {variables: {id: id}})

    return (
        <Modal show={show} className={'fade'} size='sm'>
            <div className="modal-content">
                <div className="modal-header">
                    <span className='bx bx-user me-2 text-success'/>
                    <h5 className="modal-title" id="exampleModalLabel1">{title}</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                            onClick={handleClose}/>

                </div>
                <div className="modal-body">
                    {data ? <InfoItems data={data}/> : <div className={'d-flex justify-content-center'}><Spinner animation={"border"} /></div> }
                    {error && <span className='text-danger'>{JSON.stringify(error)}</span>}
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-outline-secondary" data-bs-dismiss="modal"
                            onClick={handleClose}>
                        Close
                    </button>
                </div>
            </div>

        </Modal>
    );
};

export default InfoModal;

const InfoItems = ({data}) => {
    return (
        <>
            <div className="card bg-transparent mb-3">

                <h5 className="card-title">Resolution</h5>
                <ListGroup>
                    {data && data.channelInfo.video.map((i, k) => (
                        <ListGroup.Item className='d-flex justify-content-between' key={k}>
                            <span className='text-info'>{i.value}</span>
                            <span className='text-white'>{i.label}</span>
                        </ListGroup.Item>
                    ))}
                </ListGroup>

            </div>
            {data && data.channelInfo.langs.length > 0 &&
                <div className="card bg-transparent mb-3">

                    <h5 className="card-title">Languages</h5>
                    <ListGroup>

                        {data.channelInfo.langs.map((i, k) => (
                            <ListGroup.Item className='d-flex justify-content-between' key={k}>
                                <ReactCountryFlag countryCode={langToCountryCode(i.value)}/>
                                <span className='text-white'>{i.label}</span>
                            </ListGroup.Item>
                        ))}

                    </ListGroup>

                </div>
            }
        </>
    )
}
