import React from 'react';
import {Modal} from "react-bootstrap";
import {useFormik} from "formik";

const CategoryModal = ({show, handleClose, title,handleSave}) => {



    const formik = useFormik({
        initialValues: {
            label: ''
        },
        onSubmit: values => {
            handleSave(values)
        },
    })

    return (
        <Modal show={show} className={'fade'}>
            <div className="modal-content">
                <div className="modal-header">
                    <h5 className="modal-title" id="exampleModalLabel1">{title}</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                            onClick={handleClose}/>
                </div>
                <div className="modal-body">
                    <form>
                        <div className="mb-3">
                            <label className="form-label" htmlFor="basic-default-label">Name</label>
                            <input type="text" name={'label'} className="form-control" id="basic-default-label"
                                   value={formik.values.label} onChange={formik.handleChange}/>
                        </div>
                    </form>
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-outline-secondary" data-bs-dismiss="modal"
                            onClick={handleClose}>
                        Close
                    </button>
                    <button type="button" className="btn btn-primary" onClick={formik.handleSubmit}>Save changes
                    </button>
                </div>
            </div>

        </Modal>
    );
};

export default CategoryModal;
