import React from 'react';
import {Col, ListGroup, Modal, Row, Form} from "react-bootstrap";
import {gql, useMutation} from "@apollo/client";
import {ADD_KEY, CHANGE_KEY_TYPE, DEL_KEY} from "../store/Mutation";
import {useFormik} from "formik";
import KeyItem from "../items/KeyItem";
import {useAppApolloClient} from "../store/ApolloClient";
import KeyTypeIcon from "../component/KeyTypeIcon";
import login from "../pages/Login";

const KeysModal = ({show, handleClose, title, channelId}) => {

    const keyTypes = ['auto','audio','video']

    const client = useAppApolloClient()

    const channel = client.readFragment({
        id: `PlayableChannel:${channelId}`, // The value of the to-do item's cache ID
        fragment: gql`
            fragment Channel on PlayableChannel {
                id
                keys {
                    id
                    kid
                    key
                    type
                }
            }
        `,
    });

    const [changeKeyTypeMutation] = useMutation(CHANGE_KEY_TYPE)

    const formik = useFormik({
        initialValues: {
            kid: '',
            key: '',
            type: 'auto'
        },
        onSubmit: values => {
            addKeyMut({variables: {...values, channelId: channelId}})
                .then(r => console.log('RES', r))

        },
    })


    const [addKeyMut] = useMutation(ADD_KEY)

    const [delKeyMut] = useMutation(DEL_KEY)

    const handleDelete = (id) => {
        delKeyMut({variables: {channelId: channel.id, keyId: id}})
            .then(r => console.log(r))
    }



    const handleKeyTypeChange = (keyId,type) => {

        let newType
        let indx = keyTypes.indexOf(type)
        if (indx === 2) {
            newType = 'auto'
        } else {
            newType = keyTypes[indx + 1]
        }

        changeKeyTypeMutation({variables:{channelId:channelId,keyId:keyId,type:newType}})
            .then(r => null)

        return newType
    }

    return (
        <Modal show={show} className={'fade'} size='lg'>

            <div className="modal-content">
                <div className="modal-header">
                    <h5 className="modal-title" id="exampleModalLabel1">{title}</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                            onClick={handleClose}/>
                </div>

                <div className="modal-body">
                    <Row>
                        <Col sm={12}>
                            <div className="card bg-transparent mb-3">

                                <h5 className="card-title">Existing keys</h5>
                                <ListGroup>
                                    {channel && channel.keys.map((i, k) => (
                                        <KeyItem key={k} item={i} deleteKey={handleDelete} handleKeyTypeChange={handleKeyTypeChange}/>
                                    ))}
                                </ListGroup>

                            </div>
                        </Col>
                        <Col sm={12}>
                            <h5 className="card-title">Add key</h5>
                            <div className="card bg-transparent mb-3"
                                 style={{border: '1px solid rgba(255, 255, 255, 0.12)'}}>

                                <div className="card-body" style={{backgroundColor: 'rgba(0,0,0,.2)'}}>
                                    <Row>
                                        <Col sm={12}>

                                            <form>
                                                <Row className='mb-3'>
                                                    <Col sm={6}>
                                                        <label className="form-label"
                                                               htmlFor="basic-default-kid">Kid</label>
                                                        <input type="text" name={'kid'} maxLength={32}
                                                               className="form-control"
                                                               id="basic-default-kid"
                                                               value={formik.values.kid}
                                                               onChange={formik.handleChange}/>
                                                    </Col>
                                                    <Col sm={6}>
                                                        <label className="form-label"
                                                               htmlFor="basic-default-key">Key</label>
                                                        <input type="text" name={'key'} maxLength={32}
                                                               className="form-control"
                                                               id="basic-default-key"
                                                               onChange={formik.handleChange}/>
                                                    </Col>
                                                </Row>
                                            </form>
                                        </Col>

                                        <Col sm={12} className={'d-flex flex-grow-1 justify-content-around'}>
                                            <Form.Check
                                                inline
                                                id={'auto'}
                                                label={<>Auto <KeyTypeIcon type={'auto'}/> </>}
                                                name="type"
                                                value="auto"
                                                type="radio"
                                                checked={formik.values.type === "auto"}
                                                onChange={() => formik.setFieldValue("type", "auto")}
                                            />
                                            <Form.Check
                                                style={{textTransform:'none'}}
                                                inline
                                                id={'video'}
                                                label={<>Video <KeyTypeIcon type={'video'}/> </>}
                                                name="type"
                                                value="video"
                                                type="radio"
                                                checked={formik.values.type === "video"}
                                                onChange={() => formik.setFieldValue("type", "video")}
                                            />
                                            <Form.Check
                                                inline
                                                id={'audio'}
                                                label={<>Audio <KeyTypeIcon type={'audio'}/> </>}
                                                name="type"
                                                value="audio"
                                                type="radio"
                                                checked={formik.values.type === "audio"}
                                                onChange={() => formik.setFieldValue("type", "audio")}
                                            />
                                        </Col>

                                        <Col sm={12} className='text-center'>
                                            <div className='d-flex'>
                                                <button style={{width: '100%'}} type="button"
                                                        className="btn btn-primary"
                                                        onClick={formik.handleSubmit}>
                                                    <span className='bx bx-plus'/>
                                                    &nbsp;Assign
                                                </button>
                                            </div>
                                        </Col>
                                    </Row>
                                </div>
                            </div>
                        </Col>
                    </Row>
                </div>
            </div>

        </Modal>
    );
};

export default KeysModal;
