import React from 'react';
import {gql, useMutation, useQuery} from "@apollo/client";
import {GET_USERS} from "../store/Queries";
import {ADD_USER, DEL_USER, UPDATE_USER} from "../store/Mutation";
import {showError} from "./ShowError";

const useUsers = () => {


    const {data,error:getError} = useQuery(GET_USERS)

    const [addUserMut,{error:addError}] = useMutation(ADD_USER, {
        update(cache, {data: {addUser}}) {
            cache.modify({
                fields: {
                    users(existing = []) {
                        if (addUser) {
                            const newMediaFragment = cache.writeFragment({
                                data: addUser,
                                fragment: gql`
                                    fragment NewUser on UserEntry {
                                        id
                                    }
                                `
                            });

                            return [newMediaFragment, ...existing];
                        } else {
                            return [...existing]
                        }
                    }
                }
            })
        }
    })

    const [delUserMut,{error:delError}] = useMutation(DEL_USER, {
        update(cache, {data: {delUser}}) {
            cache.modify({
                fields: {
                    users(existing = [], {readField}) {
                        console.log('EX',existing)
                        return existing.filter(
                            providerRef => delUser !== readField('id', providerRef),
                        );
                    }
                }
            })
        }
    })

    const [updateUserMut,{error:updateError}] = useMutation(UPDATE_USER)

    React.useEffect(() => {

        if (updateError?.message) {
            showError(updateError.message)
        }
        if (getError?.message) {
            showError(getError.message)
        }
        if (delError?.message) {
            showError(delError.message)
        }
        if (addError?.message) {
            showError(addError.message)
        }
    },[getError,delError,updateError,addError])



    return [data,addUserMut,updateUserMut,delUserMut]
};

export default useUsers;
