import React from 'react';
import {Col, Form, Modal, Row} from "react-bootstrap";
import {useMutation} from "@apollo/client";
import {IMPORT_CHANNELS} from "../store/Mutation";
import {useFormik} from "formik";
import {showError, showInfo} from "../util/ShowError";

const ImportModal = ({show,providers, handleClose}) => {

    const [importChannelsMut,{error:importError}] = useMutation(IMPORT_CHANNELS)

    const formik = useFormik({
        initialValues: {
            file: '',
            provider:providers[0].name,
            lang:'ru',
            update:false,
            deleteOldKeys:false
        },
        onSubmit: values => {
            importChannelsMut({variables:{...values}})
                .then(r => {
                    if (r?.data) {
                        console.log(r.data)
                        showInfo(`New channels: ${r.data?.importChannels.inserted}, Updated: ${r.data?.importChannels.updated}`)
                    }
                })
                .then(() => handleClose())
        },
    })

    React.useEffect(() => {
        if (importError?.message) {
            showError(importError.message)
        }
    },[importError])


    return (
        <Modal show={show} className={'fade'}>
            <div className="modal-content">
                <div className="modal-header">
                    <span className='bx bx-import me-2 text-success' />
                    <h5 className="modal-title" id="exampleModalLabel1">Import channels</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                            onClick={handleClose}/>
                </div>
                <div className="modal-body">
                    <form>
                        <Row>
                            <Col>
                                <label className="form-label" htmlFor="basic-default-label">Language</label>
                                <Form.Control type="text" name="lang" value={formik.values.lang} onChange={formik.handleChange} />
                            </Col>
                            <Col>
                                <label className="form-label" htmlFor="basic-default-provider">Provider</label>
                                <Form.Select name="provider" className={'me-2'} id="basic-default-provider"
                                             value={formik.values.provider}
                                             onChange={formik.handleChange}>
                                    {providers.map((i, k) =>
                                        <option key={k} value={i.name}>{i.name}</option>
                                    )}

                                </Form.Select>
                            </Col>
                        </Row>

                        <div className="mb-3">
                            <label className="form-label" htmlFor="basic-default-label">Select json file</label>
                            <Form.Control type="file" accept=".json"
                                          onChange={e => {
                                              let file = e.target.files[0];
                                              let reader = new FileReader();
                                              reader.onload = function (item) {
                                                  formik.setFieldValue("file", item.target.result);
                                              };
                                              reader.readAsDataURL(file);
                                          }}/>

                        </div>
                        <div className="mb-1">

                            <div className={'d-flex justify-content-between'}>
                            <Form.Group className="mt-2" controlId="formBasicCheckbox">
                                <Form.Check name="update" type="checkbox" checked={formik.values.update} onChange={formik.handleChange} label={'Update if exists'}/>
                            </Form.Group>

                            <Form.Group className="mt-2" controlId="formBasicCheckbox1">
                                <Form.Check name="deleteOldKeys" disabled={!formik.values.update} type="checkbox" checked={formik.values.deleteOldKeys} onChange={formik.handleChange} label={'Delete old keys'}/>
                            </Form.Group>
                            </div>
                        </div>
                    </form>
                </div>
                <div className="modal-footer ">

                    <div>
                    <button type="button" className="btn btn-outline-secondary me-2" data-bs-dismiss="modal"
                            onClick={handleClose}>
                        Close
                    </button>
                    <button type="button" className="btn btn-primary" onClick={formik.handleSubmit}>Import
                    </button>
                    </div>
                </div>
            </div>

        </Modal>
    );
};

export default ImportModal;
