import {useLazyQuery} from "@apollo/client";
import {PLAY, RESTART, STOP} from "../store/Queries";
import {showInfo} from "./ShowError";

const useChannelControl = () => {

    const [stopQuery] = useLazyQuery(STOP, {fetchPolicy: 'network-only'})
    const [playQuery] = useLazyQuery(PLAY, {fetchPolicy: 'network-only'})
    const [restartQuery] = useLazyQuery(RESTART, {fetchPolicy: 'network-only'})

    const stop = (id) => {
        stopQuery({variables: {id: id}})
            .then(r => showInfo(`Channel ID ${r.data.stop} stopped`))
    }

    const play = (id) => {
        playQuery({variables: {id: id}})
            .then(r => console.log(r))
    }

    const restart = (id) => {
        restartQuery({variables: {id: id}})
            .then(r => showInfo(`Restarting channel ID ${r.data.restart}`))
    }

    return [play,stop,restart]
};

export default useChannelControl;
