import React from 'react';
import {usePagedMedia} from "../util/usePagedMedia";
import ChannelsHeader from "../component/ChannelsHeader";
import {useDebouncedCallback} from "use-debounce";
import ChannelModal from "../modal/ChannelModal";
import {gql, useMutation, useQuery} from "@apollo/client";
import {GET_PROVIDERS} from "../store/Queries";
import ChannelsList from "../component/list/ChannelsList";
import {ADD_CHANNEL, COPY_CHANNEL, UPDATE_CHANNEL, UPDATE_PROVIDER} from "../store/Mutation";
import ChannelsFilter from "../component/ChannelsFilter";
import ProviderModal from "../modal/ProviderModal";

const Channels = () => {
    const [handleModal, setHandleModal] = React.useState({isOpen: false, data: undefined, title: 'New channel'})
    const [handleProviderModal, setHandleProviderModal] = React.useState({isOpen: false, data: undefined, title: 'Edit provider'})

    const {data} = useQuery(GET_PROVIDERS)
    const iniVars = React.useMemo(() => {
        let filter = localStorage.getItem("filterProvider")
        if (filter) {
            return {
                limit:50,
                offset:0,
                model: {
                    searchString: '',
                    provider: filter,
                    lang:''
                }
            }
        } else {
            return {
                limit:50,
                offset:0,
                model: {
                    searchString: '',
                    provider:'',
                    lang:''
                }
            }
        }
    },[])
    const [isFilterOpen,setIsFilterOpen] = React.useState(false)



    // React.useEffect(() => {
    //     let filter = localStorage.getItem("filterProvider")
    //     if (filter) {
    //         setVariables({...variables, model: {...variables.model, lang:'', provider: filter}})
    //         // setInitFilter({provider: filter,lang: ''})
    //         // setIsFilterOpen(true)
    //        // setIsFilterOpen(true)
    //     }
    // },[])


    const {moreData, payload, variables, setVariables, hasMore} = usePagedMedia(iniVars)


    const [updateProviderMut] = useMutation(UPDATE_PROVIDER)

    const handleEditProviderModal = (providerId) => {
        let prov = data.providers.find(i => i.id === providerId)
        setHandleProviderModal({isOpen: true, data: prov, title: 'Edit provider'})
    }

    const handleEditProvider = (values) => {
        updateProviderMut({variables: {model: values}})
            .then(_ => setHandleProviderModal(prevState => ({...prevState,isOpen: false})))
    }

    const [addChannelMut] = useMutation(ADD_CHANNEL, {
        update(cache, {data: {addChannel}}) {

            cache.modify({
                fields: {
                    channels(existing = []) {

                        if (addChannel) {
                            const newMediaFragment = cache.writeFragment({
                                data: addChannel,
                                fragment: gql`
                                    fragment NewMedia on WvChannelInfo {
                                        id
                                    }
                                `
                            });

                            return {...existing, channels: [...existing.channels, newMediaFragment]};
                        } else {
                            return {...existing}
                        }
                    }
                }
            })
        }
    })

    const [copyChannelMut] = useMutation(COPY_CHANNEL, {
        update(cache, {data: {copyChannel}}) {

            cache.modify({
                fields: {
                    channels(existing = []) {

                        if (copyChannel) {
                            const newMediaFragment = cache.writeFragment({
                                data: copyChannel,
                                fragment: gql`
                                    fragment NewMedia on WvChannelInfo {
                                        id
                                    }
                                `
                            });

                            return {...existing, channels: [...existing.channels, newMediaFragment]};
                        } else {
                            return {...existing}
                        }
                    }
                }
            })
        }
    })

    const [updateChannelMut] = useMutation(UPDATE_CHANNEL)


    const debounced = useDebouncedCallback(
        // function
        (value) => {
            setVariables({...variables, model: {...variables.model, searchString: value}});
        },
        // delay in ms
        500
    );

    const handleSave = (values) => {
        addChannelMut({variables: {model: values}})
            .then(_ => setHandleModal({isOpen: false, data: undefined}))
    }

    const handleEdit = (channel) => {
        updateChannelMut({variables: {model: channel}})
            .then(_ => setHandleModal({isOpen: false, data: undefined}))
    }

    const openEditModal = (channel) => setHandleModal({isOpen: true, data: channel, title: 'Edit channel'})

    const handleCopy = (channel) => {

        let ch = {}
        ch.id = channel.id
        ch.name = channel.name + '_Copy'
        ch.url = channel.url
        ch.group = channel.group
        ch.provider = channel.provider
        ch.lang = channel.lang
        ch.vIndex = channel.vIndex


        copyChannelMut({variables: {model: ch}})
            .then(r => console.log('R', r))
    }

    const filter = React.useCallback((filter) => {
        setVariables({...variables, model: {...variables.model, lang: filter.lang, provider: filter.provider}})
    }, [])

    const toggleFilter = () => {
        if (!isFilterOpen) {
            localStorage.setItem('filterProvider','')
            setVariables({...variables, model: {...variables.model, lang:'', provider: ''}})
        }
        setIsFilterOpen(!isFilterOpen)
    }

    return (
        <>
            <ChannelsHeader debounce={debounced} filter={filter} providers={data?.providers || []}
                            handleNew={() => setHandleModal({
                                isOpen: true,
                                data: undefined,
                                title: 'New channel'
                            })}
                            toggleFilter={toggleFilter}
                            isFilterOpen={isFilterOpen}
            />
            <div className="clearfix"/>

            <div className='content-wrapper'>
                <div className='container-fluid'>

                    {handleProviderModal.isOpen &&
                        <ProviderModal data={handleProviderModal.data} title={handleProviderModal.title} show={handleProviderModal.isOpen}
                                       handleClose={() => setHandleProviderModal( prevState => ({...prevState,isOpen: false}))}
                                       handleEdit={handleEditProvider}/>
                    }

                    {handleModal.isOpen && <ChannelModal providers={data?.providers || []} show={handleModal.isOpen}
                                                         handleClose={() => setHandleModal({
                                                             isOpen: false,
                                                             data: undefined
                                                         })}
                                                         title={handleModal.title}
                                                         handleSave={handleSave} data={handleModal.data}
                                                         handleEdit={handleEdit}/>}
                    {isFilterOpen && <ChannelsFilter filter={filter} providers={data?.providers || []} /> }

                    <ChannelsList moreData={moreData} hasMore={hasMore} payload={payload || []} handleCopy={handleCopy}
                                  handleEdit={openEditModal} handleEditProvider={handleEditProviderModal} isFilterOpen={isFilterOpen}/>

                </div>
            </div>
        </>
    );
};

export default Channels;
