import React from 'react';
import {Button} from "react-bootstrap";
import ImportModal from "../modal/ImportModal";
import ExportModal from "../modal/ExportModal";

const ChannelsHeader = ({debounce, handleNew, toggleFilter,isFilterOpen,providers}) => {

    const [showImportModal,setShowImportModal] = React.useState(false)
    const [showExportModal,setShowExportModal] = React.useState(false)

    const [searchValue,setSearchValue] = React.useState('')

    React.useEffect(() => {
        debounce(searchValue)
    },[searchValue])

    return (
        <header className="topbar-nav">
            {providers.length > 1 && <ImportModal providers={providers} show={showImportModal} handleClose={() => setShowImportModal(false)} /> }
            <ExportModal show={showExportModal} handleClose={() => setShowExportModal(false)} providers={providers} />

            <nav className="navbar navbar-expand fixed-top">
                <ul className="navbar-nav me-auto align-items-center">
                    <li className="nav-item" style={{marginLeft: 250}}>
                        <form className="search-bar">
                            <input type="text" className="form-control" placeholder="Enter keywords" value={searchValue}
                                   onChange={e => setSearchValue(e.target.value)}/>
                            {/*<a href="#"><i className="icon-magnifier"></i></a></input>*/}
                        </form>
                    </li>

                    <li>
                        <Button variant='outline-light' disabled={searchValue === ''} size='sm' className='ms-2' onClick={() => setSearchValue('')}>
                            <i className="bx bx-reset"/>
                        </Button>
                    </li>
                    <li>
                        <Button variant='outline-light' active={isFilterOpen} size='sm' className='ms-2' onClick={toggleFilter}>
                            <i className="bx bx-filter-alt"/>
                        </Button>
                    </li>
                </ul>
                <ul className="navbar-nav align-items-center right-nav-link">
                    <li className="nav-item">
                        <Button variant='outline-light' className='me-2' onClick={handleNew}>
                            <i className="bx bx-plus me-2"/>New
                        </Button>
                    </li>

                    <li className="nav-item">
                        <Button variant='outline-light' className='me-2' onClick={() => setShowImportModal(true)}>
                            <i className="bx bx-import me-2"/>Import
                        </Button>
                    </li>
                    <li className="nav-item">
                        <Button variant='outline-light' className='me-2' onClick={() => setShowExportModal(true)}>
                            <i className="bx bx-export me-2"/>Export
                        </Button>
                    </li>
                </ul>

            </nav>
        </header>
    )

};

export default ChannelsHeader;
