import React from 'react';
import {Button, Form, FormControl, Row} from "react-bootstrap";
import {useDebouncedCallback} from "use-debounce";

const ChannelsFilter = ({filter, providers}) => {


    const [curlLang, setCurLang] = React.useState('')

    const [curFilter, setCurFilter] = React.useState({provider: '', lang: ''})

    const handleFilterChange = (filter) => {
        localStorage.setItem('filterProvider',filter.provider)
        setCurFilter(filter)
    }

    React.useEffect(() => {
        console.log('EFFECT',curFilter)
       //if (curFilter.provider || curFilter.lang) {
            filter(curFilter)
        //}
    }, [curFilter])

    React.useEffect(() => {
        langDebounced(curlLang)
    }, [curlLang, setCurLang])

    const langDebounced = useDebouncedCallback(
        // function
        (value) => {
            setCurFilter({...filter, lang: value})
        },
        // delay in ms
        500
    );

    const resetFilter = () => {
        localStorage.setItem('filterProvider','')
        setCurLang('')
        setCurFilter({provider: '', lang: ''})
       // filter(curFilter)
    }

    return (

        <div className="channel-filter show" style={{minHeight: 50}}>
            <Row className={'p-4'}>

                <div className={'d-flex'}>
                    <div className={'d-flex flex-grow-1'}>

                        <Form.Select className={'me-2'} aria-label="Default select example"
                                     value={curFilter.provider}
                                     onChange={e => handleFilterChange({
                                         ...filter,
                                         provider: e.target.value
                                     })}>
                            <option value={''}>Provider</option>
                            {providers.map((i, k) =>
                                <option key={k} value={i.name}>{i.name}</option>
                            )}
                        </Form.Select>

                        <FormControl placeholder={'Language'} className={'me-2'}
                                     onChange={e => setCurLang(e.target.value)}
                                     value={curlLang}/>


                    </div>
                    <div className='d-flex'>
                        <Button variant={'danger'} onClick={resetFilter}>
                            <span className="bx bx-reset"></span>&nbsp; Reset
                        </Button>
                    </div>
                </div>
            </Row>


        </div>


    );
};

export default ChannelsFilter;
