import React from 'react';
import {Button} from "react-bootstrap";

const DashboardHeader = ({debounce,state}) => {

    const [searchValue,setSearchValue] = React.useState('')

    React.useEffect(() => {
        debounce(searchValue)
    },[searchValue])

    return (
        <header className="topbar-nav">


            <nav className="navbar navbar-expand fixed-top">
                <ul className="navbar-nav me-auto align-items-center">
                    <li className="nav-item" style={{marginLeft: 250}}>
                        <form className="search-bar">
                            <input type="text" className="form-control" placeholder="Enter keywords" value={searchValue}
                                   onChange={e => setSearchValue(e.target.value)}/>
                            {/*<a href="#"><i className="icon-magnifier"></i></a></input>*/}
                        </form>
                    </li>

                    <li>
                        <Button variant='outline-light' size='sm' className='ms-2' >
                            <i className="bx bx-reset" onClick={() => setSearchValue('')}/>
                        </Button>
                    </li>

                </ul>
                <ul className="navbar-nav align-items-center right-nav-link" style={{fontSize:'0.8rem'}}>

                    <li className="nav-item me-2">
                        <span className={'text-white me-1'}>Total:</span><b>{state?.length || 0}</b>
                    </li>
                    <li className="nav-item me-2">
                        <span className={'text-success me-1'}>Online:</span><b>{state?.filter(i => i.name === "Online").length || 0}</b>
                    </li>
                    <li className="nav-item me-2">
                        <span className={'text-warning me-1'}>Buffering:</span><b>{state?.filter(i => i.name === "Buffering").length || 0}</b>
                    </li>

                </ul>

            </nav>
        </header>
    )

};

export default DashboardHeader;
