import {confirmAlert} from "react-confirm-alert";
import ConfirmModal from "../modal/ConfirmModal";

export const confirm = (message,onConfirm) => confirmAlert({
    message: message,
    customUI:({onClose}) => (<ConfirmModal message={message} onConfirm={onConfirm} onClose={() => onClose()} />),
    closeOnEscape: true,
    closeOnClickOutside: true,
})
