import React from 'react';
import {Button, Card, Spinner} from "react-bootstrap";
import {useQuery} from "@apollo/client";
import {GET_STATE_INFO} from "../../store/Queries";
import {showDateTime} from "../../util/showDateTime";
import {showError, showInfo} from "../../util/ShowError";

const StateCard = () => {

    const {data, loading, error,refetch} = useQuery(GET_STATE_INFO)

    const saveState = () => {
       fetch('http://127.0.0.1:8989/api/state/save')
           .then(r => {

               if (r.ok) {
                   r.text()
                       .then(txt => showInfo(txt))
                       .then( _ => refetch())

               } else {
                   showError('Some errors found')
               }
           })
    }

    const loadState = () => {
        fetch('http://127.0.0.1:8989/api/state/load')
            .then(r => {
                if (r.ok) {
                    r.text()
                        .then(txt => showInfo(txt))
                } else {
                    showError('Some errors found')
                }
            })
    }

    return (
        <Card>
            <Card.Body>
                {error && <h3>{error}</h3>}
                {loading && <div className={'text-center'}><Spinner animation={'border'}/></div>}
                {data &&
                    <ul className={'mb-0'}>
                        <li><b>Created:</b> {showDateTime(data.stateInfo.created)}</li>
                        <li><b>Channels:</b> {data.stateInfo.channels.map(i => <span className={'text-white'} key={i.id}>{i.name} - <i
                            className={'text-warning'}>{i.provider.name}</i> </span>)}</li>
                    </ul>
                }
            </Card.Body>

            <Card.Footer>
                <div className={'text-end'}>
                    <Button variant={"outline-success"} className={'me-2'} onClick={() => saveState()}><i className={'bx bxs-file-export me-1'}/>Save</Button>
                    <Button variant={"outline-success"} onClick={() => loadState()}><i className={'bx bxs-file-import me-1'} />Load</Button>
                </div>
            </Card.Footer>
        </Card>
    );
};

export default StateCard;
