import React from 'react';
import SimpleHeader from "../component/SimpleHeader";
import {confirm} from "../util/ConfirmAlert";
import UserItem from "../items/UserItem";
import UserModal from "../modal/UserModal";
import useUsers from "../util/useUsers";

const Providers = () => {

    const [data,addUserMut,updateUserMut,delUserMut] = useUsers()

    const [handleModal, setHandleModal] = React.useState({isOpen: false, data: undefined, title: 'Add user'})



    const handleEditModal = (provider) => setHandleModal({isOpen: true, data: provider, title: 'Edit user'})

    const handleDelete = (id) => {
        confirm('Are you sure ?', () => delUserMut({variables: {id: id}}))
    }

    const handleSave = (values) => {
        addUserMut({variables: {model: values}})
            .then(_ => setHandleModal({isOpen: false, data: undefined}))
    }

    const handleEdit = (values) => {
        updateUserMut({variables: {model: values}})
            .then(_ => setHandleModal({isOpen: false, data: undefined}))
    }

    return (
        <>
            <SimpleHeader buttonTitle='Add user'
                          handleModal={() => setHandleModal({isOpen: true, data: undefined, title: 'Add user'})}/>
            <div className="clearfix"/>
            <div className='content-wrapper'>
                <div className='container-fluid'>
                    {handleModal.isOpen &&
                        <UserModal data={handleModal.data} title={handleModal.title} show={handleModal.isOpen}
                                       handleClose={() => setHandleModal({isOpen: false, data: undefined})}
                                       handleSave={handleSave} handleEdit={handleEdit}/>
                    }

                    <div className={'list'} style={{maxHeight: 'calc(100vh - 100px)', overflowY: 'auto', overflowX:'hidden',paddingRight:10}}>
                    {data && data.users.map((i, k) => (
                        <UserItem user={i} key={k} handleDelete={handleDelete} handleEdit={handleEditModal}/>))}
                    </div>
                </div>
            </div>
        </>
    );
};

export default Providers;
