import React from 'react';

const KeyTypeIcon = ({type,onClick}) => {
    switch (type) {
        case 'auto':
            return <i className='bx bx-font me-1 text-info fs-4' style={{cursor:'pointer'}} onClick={onClick}/>
        case 'audio':
            return <i className='bx bx-volume-full me-2 text-warning fs-4' style={{cursor:'pointer'}} onClick={onClick}/>
        case 'video':
            return <i className='bx bx-video me-2 text-success fs-4' style={{cursor:'pointer'}} onClick={onClick}/>
        default:
            return <i className='bx bx-font me-2 text-success fs-4' style={{cursor:'pointer'}} onClick={onClick}/>
    }
};

export default KeyTypeIcon;
