import React from 'react';
import SimpleHeader from "../component/SimpleHeader";
import {confirm} from "../util/ConfirmAlert";
import CategoryModal from "../modal/CategoryModal";
import CategoryItem from "../items/CategoryItem";
import useCategories from "../util/useCategories";

const Categories = () => {

    const [data,addCategoryMut,delCategoryMut] = useCategories()

    const [handleModal, setHandleModal] = React.useState({isOpen: false, data: undefined})


    const handleDelete = (id) => {
        confirm('Are you sure ?', () => delCategoryMut({variables: {id: id}}))

    }

    const handleSave = (values) => {
        console.log(values)
        addCategoryMut({variables: {...values}})
            .then(_ => setHandleModal({isOpen: false, data: undefined}))
    }


    return (
        <>

            <SimpleHeader buttonTitle={'Add category'}
                          handleModal={() => setHandleModal({isOpen: true, data: undefined})}/>
            <div className="clearfix"/>
            <div className='content-wrapper'>
                <div className='container-fluid'>
                    <CategoryModal title={'Add category'} show={handleModal.isOpen}
                                   handleClose={() => setHandleModal({isOpen: false, data: undefined})} handleSave={handleSave}/>
                    <div className={'list'} style={{
                        maxHeight: 'calc(100vh - 100px)',
                        overflowY: 'auto',
                        overflowX: 'hidden',
                        paddingRight: 10
                    }}>
                        {data && data.categories.map((i, k) => (
                            <CategoryItem category={i} key={k} handleDelete={handleDelete} />))}
                    </div>
                </div>
            </div>
        </>
    );
};

export default Categories;
