export  const langToCountryCode = (lang) => {
    switch (lang) {
        case 'en':
            return 'gb'
        case 'dan':
            return 'dk'
        case 'da':
            return 'dk'
        case 'rus':
            return 'ru'
        case 'spa':
            return 'es'
        case 'uk':
            return 'gb'
        case 'tur':
            return 'tr'
        case 'lit':
            return 'lt'
        case 'lav':
            return 'lv'
        default:
            return lang
    }
}
