import React, {useMemo} from 'react';
import {useLazyQuery} from "@apollo/client";
import {CHANNEL_BY_IDS} from "../store/Queries";
import OnlineChannelsList from "../component/list/OnlineChannelsList";
import DashboardHeader from "../component/DasboardHeader";
import {useDebouncedCallback} from "use-debounce";
import useWebSocket from "react-use-websocket";
import {WS_URL} from "../Constant";

const Dev = () => {


    const [filtered, setFiltered] = React.useState([])

    const [isSearch, setIsSearch] = React.useState(false)

    const {
        lastJsonMessage
    } = useWebSocket(WS_URL, {
        //Will attempt to reconnect on all close events, such as server shutting down
        shouldReconnect: (closeEvent) => true,
    });

    let activeIds = useMemo(() => {
        return lastJsonMessage?.map(i => i.id) || []
    }, [lastJsonMessage?.length])

    const [getChannels, {loading, data}] = useLazyQuery(CHANNEL_BY_IDS)


    React.useEffect(() => {
        getChannels({variables: {ids: activeIds}})
    }, [activeIds])


    React.useEffect(() => {
        if (data?.channelsById) {
            if (!isSearch) {
                setFiltered(data.channelsById)
            }
        }
    }, [data?.channelsById, isSearch])

    const debounced = useDebouncedCallback(
        // function
        (value) => {
            if (value) {
                setIsSearch(true)
                let filtered = data?.channelsById.filter((ch) => ch.id == value || ch.name.toLowerCase().includes(value.toLowerCase()))

                setFiltered(filtered);
            } else {
                setIsSearch(false)
            }

        },
        // delay in ms
        500
    );



    return (
        <>
            <DashboardHeader debounce={debounced} state={lastJsonMessage}/>

            <div className='content-wrapper'>
                <div className='container-fluid'>
                   <OnlineChannelsList onlineChannels={filtered || []} state={lastJsonMessage}/>
                </div>
            </div>
        </>
    );
};

export default Dev;
