import React from 'react';
import InfiniteScroll from "react-infinite-scroll-component";
import ChannelItem from "../../items/ChannelItem";
import {useMutation} from "@apollo/client";
import {DEL_CHANNEL} from "../../store/Mutation";
import {confirm} from "../../util/ConfirmAlert";
import KeysModal from "../../modal/KeysModal";
import InfoModal from "../../modal/InfoModal";
import useWebSocket from "react-use-websocket";
import {WS_URL} from "../../Constant";
import useChannelControl from "../../util/useChannelControl";


const ChannelsList = ({moreData, hasMore, payload, handleCopy, handleEdit,handleEditProvider, isFilterOpen}) => {


    const {
        lastJsonMessage
    } = useWebSocket(WS_URL, {
        //Will attempt to reconnect on all close events, such as server shutting down
        shouldReconnect: (closeEvent) => true,
    });

    const [play,stop,restart] = useChannelControl()

    const [keyModalOpen, setKeysModalOpen] = React.useState({isOpen: false, channelId: 0})
    const [infoModalOpen, setInfoModalOpen] = React.useState({isOpen: false, channelId: 0})

    const scrollableRef = React.useRef(null)
    const [delChannelMut] = useMutation(DEL_CHANNEL, {
        update(cache, {data: {delChannel}}) {
            cache.modify({
                fields: {
                    channels(existing = [], {readField}) {
                        console.log('EX', existing)
                        let channels = existing.channels.filter(
                            channelRef => delChannel !== readField('id', channelRef),
                        );
                        return {...existing, channels: channels};
                    }
                }
            })
        }
    })



    const handleDelete = (id) => {
        confirm('Are you sure ?', () => delChannelMut({variables: {id: id}}))
    }

    const openKeysModal = (channelId) => {
        console.log('CH',channelId)
        setKeysModalOpen({isOpen: true, channelId: channelId})
    }

    const handleInfo = (id) => {
        setInfoModalOpen({isOpen: true,channelId: id})
    }

    const max_height = React.useMemo(() => {
        if (isFilterOpen) {

            return 'calc(100vh - 210px)'
        } else {

            return 'calc(100vh - 100px)'
        }
    }, [isFilterOpen])


    return (
        <>
            <InfoModal show={infoModalOpen.isOpen} title={"Channel info"}
                       handleClose={() => setInfoModalOpen({isOpen: false, channelId: infoModalOpen.channelId})}
                       id={infoModalOpen.channelId}/>
            <KeysModal show={keyModalOpen.isOpen} title={'Key management'}
                       handleClose={() => setKeysModalOpen({isOpen: false, channelId: 0})}
                       channelId={keyModalOpen.channelId}/>
            <div id="scrollableDiv" ref={scrollableRef} className={'list'}
                 style={{maxHeight: max_height, overflowY: 'auto', overflowX: 'hidden', paddingRight: 10}}>
                <InfiniteScroll next={moreData} hasMore={hasMore} loader={<p style={{maxWidth: 100}}>Loading</p>}
                                dataLength={payload?.length || 0} scrollableTarget="scrollableDiv" style={{overflow:'hidden'}}>
                    {payload?.map((item, key) => (
                        <ChannelItem key={key} handleCopy={handleCopy} channel={item} play={play} stop={stop} restart={restart}
                                     handleDelete={handleDelete} handleEdit={handleEdit} openKeysModal={openKeysModal}
                                     state={lastJsonMessage?.find(i => i.id === item.id)}
                                     handleInfo={handleInfo} handleEditProvider={handleEditProvider}/>))}
                </InfiniteScroll>
            </div>
        </>
    );
};

export default ChannelsList;
