import React from 'react';
import '../assets/css/page-auth.css'
import {useFormik} from "formik";
import {Navigate} from "react-router-dom";
import {showError} from "../util/ShowError";
import {useAuthToken} from "../util/auth";
import {useMutation} from "@apollo/client";
import {LOGIN} from "../store/Mutation";
import {ToastContainer} from "react-toastify";

const Login = () => {

    const [_,setAuthToken,removeAuthToken] = useAuthToken();

    React.useEffect(() => {
        removeAuthToken()
    },[])

    const [loginSuccess, setLoginSuccess] = React.useState(false)


    const [loginMutation, {error}] = useMutation(LOGIN,{onCompleted:data => handleSuccessLogin(data)})


    const formik = useFormik({
        initialValues: {
            login: '',
            password: ''
        },
        onSubmit: values => {
            console.log(values)
            loginMutation({variables:{...values}})
              //  .then(r => setLoginSuccess(true))
            // addProviderMut({variables: {model: values}})
            //     .then(r => console.log('RES', r))
            // alert(JSON.stringify(values, null, 2));
        },
    })

    const handleSuccessLogin = (data) => {
        if (data?.login && data.login.length > 0) {
            setAuthToken(data.login)
            setLoginSuccess(true)
        }
    }

    React.useEffect(() => {
        if (error?.message) {
            showError(error.message)
            console.log('HANDLE',error.message)
        }
    },[error])


    return (
        <div className="container-xxl">
            <ToastContainer />
            <div className="authentication-wrapper authentication-basic container-p-y">

                <div className="authentication-inner">

                    <div className="card">
                        <div className="card-body">


                            <div className={'text-center'}>
                                <h4>MPDPlayer UI 2.0</h4>
                            </div>
                            <hr/>



                            {loginSuccess && <Navigate to='/' replace/>}
                            <form onSubmit={formik.handleSubmit}>
                                <div className="mb-3">
                                    <label htmlFor="login" className="form-label">Username</label>
                                    <input
                                        type="text"
                                        className="form-control"
                                        id="login"
                                        name="login"
                                        placeholder="Enter your username"
                                        autoFocus
                                        value={formik.values.login}
                                        onChange={formik.handleChange}
                                    />
                                </div>
                                <div className="mb-3 form-password-toggle">

                                    <label className="form-label" htmlFor="password">Password</label>

                                    <input
                                        type="password"
                                        id="password"
                                        className="form-control"
                                        name="password"
                                        value={formik.values.password}

                                        onChange={formik.handleChange}
                                    />


                                </div>
                                <div className="mb-3">
                                    <button className="btn btn-primary d-grid w-100" type="submit">Sign in</button>
                                </div>
                            </form>

                        </div>
                    </div>

                </div>
            </div>
        </div>
    );
};

export default Login;
