import React from 'react';

const ConfirmModal = ({message,onClose,onConfirm}) => {

    const confirmAndClose = () => {
        onConfirm()
        onClose()
    }

    return (
        <div className="modal fade show" id="modalCenter" tabIndex="-1" aria-modal="true" role="dialog"
             style={{display: "block"}}>
            <div className="modal-dialog modal-dialog-centered" role="document">
                <div className="modal-content">
                    {/*<div className="modal-header">*/}
                    {/*    <h5 className="modal-title" id="modalCenterTitle">Modal title</h5>*/}
                    {/*    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>*/}
                    {/*</div>*/}
                    <div className="modal-body">
                        <h4>{message}</h4>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-outline-secondary" data-bs-dismiss="modal" onClick={onClose}>
                            No
                        </button>
                        <button type="button" className="btn btn-primary" onClick={
                          confirmAndClose
                        }>Yes</button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ConfirmModal;
