import {gql} from "@apollo/client";

export const GET_CHANNELS = gql`
    query Channels($limit:Int!,$offset:Long!,$model:ChannelFilterModel!) {
        channels(limit: $limit,offset: $offset,model:$model) {
            hasMore
            channels {
                id
                name
                provider {
                    id
                    name
                    format
                }
                keys {
                    id
                    kid
                    key
                    type
                }
                lang
                url
                vIndex
            }
        }
    }
`

export const GET_STATE_INFO = gql`
    query StateInfo {
        stateInfo {
            created
            channels {
                id
                name
                provider {
                    id
                    name
                }
            }
        }
    }
`;

export const GET_PROVIDERS = gql`
    query Providers {
        providers {
            id
            name
            ua
            x_forwarder
            trimToLive
            availabilityCheck
            format
            proxy
            engine
        }
    }
`

export const GET_CATEGORIES = gql`
    query GetCategories {
        categories {
            id
            label
        }
    }
`

export const GET_USERS = gql`
    query GetUsers {
        users {
            id
            login
            expiration
            role
        }
    }
`
export const PLAY = gql`
    query Play($id:Int!) {
        play(id:$id)
    }
`
export const STOP = gql`
    query Stop($id:Int!) {
        stop(id:$id)
    }
`

export const RESTART = gql`
    query Restart($id:Int!) {
        restart(id:$id)
    }
`

export const ACTIVE_CHANNELS = gql`
    query ActiveChannels {
     activeChannels {
         name
         id
         provider {
             id
             name
             format
             engine
         }
     }
    }
`
export const CHANNEL_BY_IDS = gql`
    query ChannelsByIds($ids:[Int!]!) {
        channelsById(ids: $ids) {
            name
            id
            provider {
                id
                name
                format
                engine
            }
        }
    }
`

export const CHANNEL_INFO = gql`
    query ChannelInfo($id:Int!) {
        channelInfo(id: $id) {
            video {
                value
                label
            }
            langs {
                value
                label
            }
        }
    }
`

export const ONLINE_CHANNELS = gql`
    query OnlineChannels($ids:[Int!]!) {
        onlineChannels(ids: $ids) {
            id
            name
            lang
            provider {
                name
                format
            }
        }
    }
`
