import React from 'react';
import {useQuery} from "@apollo/client";
import {GET_CHANNELS} from "../store/Queries";

export const usePagedMedia = (initVars) => {


    const [variables, setVariables] = React.useState(initVars)


    // React.useEffect(() => {
    //     const scrollable = document.getElementById("scrollableDiv");
    //     scrollable.scrollTop = 0
    // },[variables.filter])

    const {data,loading,fetchMore} = useQuery(GET_CHANNELS, {variables:variables,fetchPolicy:"cache-and-network",nextFetchPolicy:"cache-first"})

    //console.log('DATA LEN',data?.mediaPaged.mediaItems.length)



    const moreData = () => {
        if (data) {

            fetchMore({
                variables: {
                    //model:{...variables.model,offset:data.mediaPaged.mediaItems.length}

                    offset: data.channels.channels.length
                },
                updateQuery: (prev, {fetchMoreResult}) => {
                    if (!fetchMoreResult) return prev;

                    return Object.assign({}, prev, {
                        channels: {
                            hasMore: fetchMoreResult.channels.hasMore,
                            channels: [...prev.channels.channels, ...fetchMoreResult.channels.channels]
                        }
                    });
                }
            })
        }
    }


    return {moreData,payload:data?.channels.channels,variables,setVariables,hasMore:data?.channels.hasMore,busy:loading}

};

