import React from 'react';
import {Form, Modal} from "react-bootstrap";
import {useFormik} from "formik";

const ChannelModal = ({show,handleClose,title,handleSave,handleEdit,providers,data}) => {

    const formik = useFormik({
        initialValues: data ? data : {
            name: '',
            url: '',
            group: '',
            provider: '',
            lang: 'ru',
            vIndex:0
        },
        onSubmit: values => {
            if (data) {
                let edited = {
                    id:data.id,
                    name:values.name,
                    url: values.url,
                    group: values.group,
                    provider: typeof values.provider === 'object' ? values.provider.name : values.provider,
                    lang: values.lang,
                    vIndex: values.vIndex
                }
                handleEdit(edited)
            } else {
                handleSave(values)
            }
        }
    })

    return (
        <Modal show={show} className={'fade'} >
                <div className="modal-content">
                    <div className="modal-header">
                        <span className='bx bx-tv me-2 text-success' />
                        <h5 className="modal-title" id="exampleModalLabel1">{title}</h5>
                        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" onClick={handleClose}/>
                    </div>
                    <div className="modal-body">
                        <form>
                            <div className="form-group">
                                <label className="form-label" htmlFor="basic-default-name">Name</label>
                                <input type="text" name={'name'} className="form-control" id="basic-default-name"
                                       value={formik.values.name} onChange={formik.handleChange}/>
                            </div>
                            <div className="mb-3">
                                <label className="form-label" htmlFor="basic-default-url">Link</label>
                                <input type="text" name={'url'} value={formik.values.url} className="form-control" id="basic-default-url"
                                       onChange={formik.handleChange}/>
                            </div>

                            <div className="mb-3">
                                <label htmlFor="html5-number-input" className="form-label">Video representation index</label>

                                <input name="vIndex" value={formik.values.vIndex} className="form-control" type="number" onChange={formik.handleChange} id="html5-number-input"/>

                            </div>

                            <div className="mb-3">
                                <label className="form-label" htmlFor="basic-default-provider">Provider</label>
                                <Form.Select name="provider" className={'me-2'} id="basic-default-provider"
                                             value={formik.values.provider.name}
                                             onChange={formik.handleChange}>
                                    <option value={''}>Provider</option>

                                    {providers.map((i, k) =>
                                        <option key={k} value={i.name}>{i.name}</option>
                                    )}

                                </Form.Select>
                            </div>
                            <div className="mb-3">
                                <label className="form-label" htmlFor="basic-default-lang">Language</label>
                                <input type="text" value={formik.values.lang} name='lang' className="form-control" id="basic-default-lang"
                                       onChange={formik.handleChange}/>
                            </div>
                        </form>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-outline-secondary" data-bs-dismiss="modal" onClick={handleClose}>
                            Close
                        </button>
                        <button type="button" className="btn btn-primary" onClick={formik.handleSubmit}>Save changes</button>
                    </div>
                </div>

        </Modal>
    );
};

export default ChannelModal;
