import React from 'react';
import {Modal} from "react-bootstrap";
import "react-datepicker/dist/react-datepicker.css";
import ReactPlayer from "react-player";

const PlayerModal = ({show, handleClose, title, id}) => {


    return (
        <Modal show={show} className={'fade'} size='lg' centered>
            <div className="modal-content">
                <div className="modal-header align-items-start">
                    <span style={{fontSize:'1.35rem'}} className='bx bx-movie-play me-2 text-primary'/>
                    <span>{title}</span>
                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                            onClick={handleClose}/>

                </div>
                <div className="modal-body">
                    <ReactPlayer url={`/${id}/hls/playlist.m3u8`} playing={true} controls width='100%' height='100%' />
                </div>

            </div>

        </Modal>
    );
};

export default PlayerModal;

