import React from 'react';
import {Button, ButtonGroup, Col, Row} from "react-bootstrap";
import {showDate} from "../util/showDateTime";

const UserItem = ({user,handleDelete,handleEdit}) => {
    return (

                <div className={'card mb-3'}>
                    <Row className='align-items-center'>

                        <Col sm={1} className={'text-center'}>
                            <span className='bx bx-user'/>
                        </Col>
                        <Col>
                            {user.login}
                        </Col>
                        <Col className={user.role === 'ADMIN' ? 'text-danger' : 'text-success'}>
                            <b>{user.role}</b>
                        </Col>
                        <Col>
                            Expired: <em>{showDate(user.expiration)  }</em>
                        </Col>

                        <Col className='d-flex justify-content-end'>
                            <ButtonGroup>
                                <Button variant='outline-secondary channel-control' onClick={() => handleEdit(user)}>
                                    <span className="tf-icons bx bx-edit"></span>
                                </Button>
                                <Button variant='outline-secondary channel-control text-danger' onClick={() => handleDelete(user.id)}>
                                    <span className="tf-icons bx bx-trash-alt"></span>
                                </Button>
                            </ButtonGroup>
                        </Col>
                    </Row>
                </div>
    );
};

export default UserItem;
