import React from 'react';
import {Col, Form, Modal, Row} from "react-bootstrap";
import {useMutation} from "@apollo/client";
import {EXPORT_CHANNELS} from "../store/Mutation";
import {useFormik} from "formik";
import {showError, showInfo} from "../util/ShowError";
import {exportTypes} from "../Constant";

const ExportModal = ({show, handleClose,providers}) => {

    const [exportChannelsMut,{error:exportError}] = useMutation(EXPORT_CHANNELS)

    const formik = useFormik({
        initialValues: {
            type: exportTypes[0].value,
            host: '127.0.0.1',
            provider:'All'
        },
        onSubmit: values => {
            exportChannelsMut({variables:{...values}})
                .then(r => {
                    if (r?.data) {
                        showInfo(`Exported to ${r.data?.exportChannels}`)
                    }
                })
                .then(() => handleClose())
        },
    })

    React.useEffect(() => {
        if (exportError?.message) {
            showError(exportError.message)
        }
    },[exportError])


    return (
        <Modal show={show} className={'fade'}>
            <div className="modal-content">
                <div className="modal-header">
                    <span className='bx bx-import me-2 text-success' />
                    <h5 className="modal-title" id="exampleModalLabel1">Import channels</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                            onClick={handleClose}/>
                </div>
                <div className="modal-body">
                    <form>
                        <Row>
                            <Col>
                                <label className="form-label" htmlFor="basic-default-label">Host in playlist</label>
                                <Form.Control type="text" name="host" value={formik.values.host} onChange={formik.handleChange}/>
                            </Col>
                            <Col>
                                <label className="form-label" htmlFor="basic-default-provider">Type</label>
                                <Form.Select  name="type" className={'me-2'} id="basic-default-provider"
                                              value={formik.values.type}
                                              onChange={formik.handleChange}>
                                    {exportTypes.map((i, k) =>
                                        <option key={k} value={i.value}>{i.name}</option>
                                    )}

                                </Form.Select>
                            </Col>
                            <div className="mb-3">
                                <label className="form-label" htmlFor="basic-default-provider">Provider</label>
                                <Form.Select name="provider" className={'me-2'} id="basic-default-provider"
                                             value={formik.values.provider}
                                             onChange={formik.handleChange}>
                                    <option value={'All'}>All</option>

                                    {providers.map((i, k) =>
                                        <option key={k} value={i.name}>{i.name}</option>
                                    )}

                                </Form.Select>
                            </div>
                        </Row>
                    </form>
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-outline-secondary" data-bs-dismiss="modal"
                            onClick={handleClose}>
                        Close
                    </button>
                    <button type="button" className="btn btn-primary" onClick={formik.handleSubmit}>Export
                    </button>
                </div>
            </div>

        </Modal>
    );
};

export default ExportModal;
