import {gql} from "@apollo/client";

export const ADD_CHANNEL = gql`
    mutation AddChannel($model: WvChannelInfoInput!) {
        addChannel(model:$model) {
            id
        }
    }
`

export const UPDATE_CHANNEL = gql`
    mutation UpdateChannel($model: WvChannelInfoInput!) {
        updateChannel(model:$model) {
            id
            name
            provider {
                id
                name
                format
            }
            lang
            url
            vIndex
        }
    }
`

export const COPY_CHANNEL = gql`
    mutation CopyChannel($model: WvChannelInfoInput!) {
        copyChannel(model:$model) {
            id
        }
    }
`

export const ADD_PROVIDER = gql`
    mutation AddProvider($model: ProviderEntryInput!) {
        addProvider(model:$model) {
            id
            name
            ua
            availabilityCheck
            trimToLive
            x_forwarder
            format
            proxy
            engine
        }
    }
`

export const UPDATE_PROVIDER = gql`
    mutation UpdateProvider($model: ProviderEntryInput!) {
        updateProvider(model:$model) {
            id
            name
            ua
            availabilityCheck
            trimToLive
            x_forwarder
            format
            proxy
            engine
        }
    }
`

export const UPDATE_CATEGORY= gql`
    mutation UpdateProvider($model: ProviderEntryInput!) {
        updateProvider(model:$model) {
            id
            name
            ua
            availabilityCheck
            trimToLive
            x_forwarder
        }
    }
`

export const ADD_CATEGORY = gql`
    mutation AddCategory($label: String!) {
        addCategory(label:$label) {
            id
            label
        }
    }
`

export const ADD_KEY = gql`
    mutation AddKey($channelId:Int!,$kid:String,$key:String!,$type:String!) {
        addKey(channelId: $channelId,kid:$kid,key: $key,type:$type) {
            id
            keys {
                id
                kid
                key
                type
            }
        }
    }
`

export const CHANGE_KEY_TYPE = gql`
    mutation ChangeKeyType($channelId:Int!,$keyId:Int!,$type:String!) {
        changeKeyType(channelId: $channelId,keyId: $keyId,type:$type) {
            id
            keys {
                id
                kid
                key
                type
            }
        }
    }
`

export const ADD_USER = gql`
    mutation AddUser($model: UserInputModel!) {
        addUser(model: $model) {
            id
            login
            expiration
            role
        }
    }
`

export const DEL_KEY = gql`
    mutation DelKey($channelId: Int!,$keyId:Int!) {
        delKey(channelId:$channelId,keyId: $keyId) {
            id
            keys {
                id
                kid
                key
                type
            }
        }
    }
`
export const DEL_CHANNEL = gql`
    mutation DelKey($id: Int!) {
        delChannel(id:$id)
    }
`
export const DEL_PROVIDER = gql`
    mutation DelKey($id: Int!) {
        delProvider(id:$id)
    }
`

export const UPDATE_USER = gql`
    mutation UpdateUser($model:UserInputModel!) {
        updateUser(model: $model) {
            id
            login
            expiration
            role
        }
    }
`

export const DEL_CATEGORY = gql`
    mutation DelCategory($id: Int!) {
        delCategory(id:$id)
    }
`

export const DEL_USER = gql`
    mutation DelUser($id: UUID!) {
        delUser(id:$id)
    }
`

export const LOGIN = gql`
    mutation login($login: String!, $password: String!) {
        login(login: $login, password: $password)
    }
`;

export const IMPORT_CHANNELS = gql`
    mutation ImportChannels($file:String!,$provider:String!,$lang:String!,$update:Boolean!,$deleteOldKeys:Boolean!) {
        importChannels(file: $file, provider:$provider,lang: $lang,update:$update,deleteOldKeys:$deleteOldKeys) {
            inserted
            updated
        }
    }
`
export const EXPORT_CHANNELS = gql`
    mutation ExportChannels($host:String!,$type:String!,$provider:String!) {
        exportChannels(host: $host, type: $type, provider: $provider)
    }
`
