import React from 'react';
import {Badge, Button, ButtonGroup, Col, Row, Spinner} from "react-bootstrap";
import ReactCountryFlag from "react-country-flag";
import ChannelDropdown from "../component/ChannelDropdown";
import {langToCountryCode} from "../util/LangToCuntryCode";

const ChannelItem = ({channel, handleDelete, openKeysModal, handleCopy, handleEdit,handleEditProvider,play,stop,restart,handleInfo,state}) => {

    let isTs = channel.provider.format === "MPEG-TS"

    let isInit = state?.name === "Initialize" || false

    const StateIcon = () => {
        if (isTs) return <span className='bx bx-tv text-success'/>
        switch (state?.name) {
            case "Online":
                return <div className={'d-flex flex-column'}><i className='bx bx-tv text-success mb-1'/><small>{state?.status.time}</small></div>
            case "Buffering":
                return <div className={'d-flex flex-column'}><i className='bx bx-tv text-warning mb-1'/><small className={'text-white'}>Buffering</small></div>
            case "Initialize":
                return <div className={'d-flex flex-column align-items-center'}><Spinner size={'sm'} animation={'grow'} className='text-success mb-1'/><small className={'text-white'}>Initialize</small></div>

            default:
                return <span className='bx bx-tv'/>
        }
    }

    return (

                <div className={'card mb-3'}>
                    <Row className='align-items-center'>

                        {/*{JSON.stringify(state)}*/}
                        <Col sm={1} className={'text-center'}>
                            <StateIcon />
                        </Col>
                        <Col>{channel.id}</Col>
                        <Col className={'column-clickable'} style={{fontSize:'0.8rem'}} onClick={() => handleEdit(channel)}>
                            {channel.name}
                        </Col>
                        <Col>
                            {channel.group}
                        </Col>
                        <Col className={'column-clickable'} onClick={() => handleEditProvider(channel.provider.id)}>
                            {channel.provider.name}
                        </Col>
                        <Col style={{fontSize:'1.8rem'}}>
                            {channel.lang && <ReactCountryFlag countryCode={langToCountryCode(channel.lang)}/>}
                        </Col>
                        <Col className={'column-clickable'} onClick={() => openKeysModal(channel.id)}>
                            <i className='bx bx-key me-1 fs-4' />
                            <Badge bg={channel.keys.length > 0 ? 'success':'danger'}>{channel.keys.length}</Badge>
                        </Col>
                        {/*<Col>*/}
                        {/*    <Button variant='info' onClick={() => info(channel.id)}><span className='bx bx-info-circle' />Info</Button>*/}
                        {/*</Col>*/}
                        <Col style={{minWidth:220}} className={'text-end'}>
                            {state && (state.name === "Buffering" || state.name === "Online") ?
                                <ButtonGroup aria-label="Basic example">
                                <Button className={'my-1'} variant='danger' onClick={() => stop(channel.id)}>
                                    <span><i className='bx bx-stop me-1' />Stop</span>
                                </Button>
                                    <Button disabled={state?.name === "Buffering"} className={'my-1'} variant='warning' onClick={() => restart(channel.id)}>
                                        <div className={'d-flex flex-column'}>
                                            <span> <i className='bx bx-reset' /> Restart</span>
                                        </div>
                                    </Button>
                                </ButtonGroup>
                                :
                                  <Button disabled={isTs || isInit} variant={isTs ? 'dark' : 'success'} onClick={() => play(channel.id)}>
                                      {isTs ? <span><span className='bx bx-block me-1 text-danger' />MPEG-TS</span> : <span><span className='bx bx-play-circle me-1' />Play</span>}</Button>

                            }
                        </Col>
                        <div className='d-flex justify-content-center' style={{width:70}}>

                            <ChannelDropdown openKeysModal={() => openKeysModal(channel.id)}
                                             handleEdit={() => handleEdit(channel)}
                                             handleCopy={() => handleCopy(channel)}
                                             handleDelete={() => handleDelete(channel.id)}
                                             handleInfo={() => handleInfo(channel.id)}
                            />

                        </div>
                    </Row>
                </div>
    );
};

export default ChannelItem;
