

export const showDateTime = (date) => {
    date = new Date(date);
    let year = date.getFullYear();
    let month = date.getMonth()+1;
    let dt = date.getDate();
    let hour = date.getHours();
    let minutes = date.getMinutes();
    let sec = date.getSeconds()

    if (dt < 10) {
        dt = '0' + dt;
    }
    if (month < 10) {
        month = '0' + month;
    }

    if (minutes < 10) {
        minutes = '0' + minutes;
    }


    return (
        dt + '-' + month + '-'+year + ' ' + hour + ':' + minutes
    )
};

export const showDate = (date) => {
    date = new Date(date);
    let year = date.getFullYear();
    let month = date.getMonth()+1;
    let dt = date.getDate();

    if (dt < 10) {
        dt = '0' + dt;
    }
    if (month < 10) {
        month = '0' + month;
    }



    return (
        dt + '-' + month + '-'+year
    )
};
