import React from 'react';
import {ListGroup} from "react-bootstrap";
import KeyTypeIcon from "../component/KeyTypeIcon";

const KeyItem = ({item,deleteKey,handleKeyTypeChange}) => {

    const [type,setType] = React.useState(item.type)

    const changeKeyType = (type) => {
        let newType = handleKeyTypeChange(item.id,type)
        setType(newType)
    }

    return (
        <ListGroup.Item>
            <div className='d-flex justify-content-between'>
                <span>
                <i className={'bx bx-sort text-muted'}/>
                <KeyTypeIcon type={type} onClick={() => changeKeyType(type)}/>
                    </span>
                <span style={{color:'#ccc'}}>{item.kid}:{item.key}</span>
                <span className='bx bx-minus-circle' style={{color:"darkred",cursor:"pointer",fontSize:'1.5rem'}} onClick={() => deleteKey(item.id)}/>
            </div>
        </ListGroup.Item>
    );
};

export default KeyItem;
