import React from 'react';
import {gql, useMutation, useQuery} from "@apollo/client";
import {GET_PROVIDERS} from "../store/Queries";
import {ADD_PROVIDER, DEL_PROVIDER, UPDATE_PROVIDER} from "../store/Mutation";
import {showError} from "./ShowError";

const useProviders = () => {

    const {data,error:getError} = useQuery(GET_PROVIDERS)

    const [addProviderMut,{error:addError}] = useMutation(ADD_PROVIDER, {
        update(cache, {data: {addProvider}}) {
            cache.modify({
                fields: {
                    providers(existing = []) {
                        if (addProvider) {
                            const newMediaFragment = cache.writeFragment({
                                data: addProvider,
                                fragment: gql`
                                    fragment NewProvider on ProviderEntry {
                                        id
                                    }
                                `
                            });

                            return [newMediaFragment, ...existing];
                        } else {
                            return [...existing]
                        }
                    }
                }
            })
        }
    })

    const [delProviderMut,{error:delError}] = useMutation(DEL_PROVIDER, {
        update(cache, {data: {delProvider}}) {
            console.log('UPDATE!!')
            cache.modify({
                fields: {
                    providers(existing = [], {readField}) {
                        return existing.filter(
                            providerRef => delProvider !== readField('id', providerRef),
                        );
                    }
                }
            })
        }
    })

    const [updateProviderMut,{error:updateError}] = useMutation(UPDATE_PROVIDER)



    React.useEffect(() => {

        if (updateError?.message) {
            showError(updateError.message)
        }
        if (getError?.message) {
            showError(getError.message)
        }
        if (delError?.message) {
            showError(delError.message)
        }
        if (addError?.message) {
            showError(addError.message)
        }
    },[getError,delError,updateError,addError])


    return [data,addProviderMut,updateProviderMut,delProviderMut]
};

export default useProviders;
