import React from 'react';
import {Col, Form, Modal, Row} from "react-bootstrap";
import {useFormik} from "formik";
import {Role} from "../Constant";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

const UserModal = ({show, handleClose, title,data,handleSave,handleEdit}) => {
    console.log('DARA',data)
    const [startDate,setStartDate] = React.useState(data && data?.expiration !== 0 ? new Date(data.expiration) : new Date())

    const formik = useFormik({
        initialValues: data ? data : {
            login: '',
            pass: '',
            expiration: '',
            role:'USER'
        },
        onSubmit: values => {
            if (data) {
                let edited = {
                    id:data.id,
                    login: values.login,
                    pass:values.pass,
                    expiration: values.expiration,
                    role:values.role
                }
                handleEdit(edited)
            } else {
                handleSave(values)
            }
        }
    })

    const handleDateChange = (date) => {
        formik.setFieldValue('expiration',date.getTime())
        setStartDate(date)
    }

    return (
        <Modal show={show} className={'fade'}>
            <div className="modal-content">
                <div className="modal-header">
                    <span className='bx bx-user me-2 text-success' />
                    <h5 className="modal-title" id="exampleModalLabel1">{title}</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"
                            onClick={handleClose}/>

                </div>
                <div className="modal-body">

                    <form>
                        <div className="mb-3">
                            <label className="form-label" htmlFor="basic-default-name">Username</label>
                            <input type="text" name={'login'} className="form-control" id="basic-default-name"
                                   value={formik.values.login} onChange={formik.handleChange}/>
                        </div>
                        <div className="mb-3">
                            <label className="form-label" htmlFor="basic-default-ua">Password</label>
                            <input type="password" name={'pass'} className="form-control" id="basic-default-ua"
                                   onChange={formik.handleChange} value={formik.values.pass}/>
                            {data && <small>Leave blank if not changing</small> }
                        </div>
                        <div className="mb-3">
                            <Row>
                                <Col>
                                    <label className="form-label" htmlFor="basic-default-forwarder">Expiration</label>
                                    <DatePicker selected={startDate} onChange={(date) => handleDateChange(date)} className="form-control"/>
                                </Col>
                                <Col>
                                    <label className="form-label" htmlFor="basic-default-role">Role</label>
                                    <Form.Select name="role" className={'me-2'} id="basic-default-role"
                                                 value={formik.values.role}
                                                 onChange={formik.handleChange}>
                                        {Role.map((i, k) =>
                                            <option key={k} value={i.toUpperCase()}>{i}</option>
                                        )}

                                    </Form.Select>
                                </Col>
                            </Row>

                        </div>

                    </form>
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-outline-secondary" data-bs-dismiss="modal"
                            onClick={handleClose}>
                        Close
                    </button>
                    <button type="button" className="btn btn-primary" onClick={formik.handleSubmit}>Save changes
                    </button>
                </div>
            </div>

        </Modal>
    );
};

export default UserModal;
