import React from 'react';
import {Button, ButtonGroup, Col, Row, Spinner} from "react-bootstrap";
import {capitalizeFirstLetter} from "../util/misc";

const OnlineChannelItem = ({channel, stop, watch,state}) => {

    const StateIcon = () => {
        if (channel.provider.format === "MPEG-TS") return <span className='bx bx-tv text-success'/>
        switch (state?.name) {
            case "Online":
                return <span className='bx bx-tv text-success'/>
            case "Buffering":
                return <div className={'d-inline-flex align-items-center'}>
                    <Spinner className={'me-2'} size={'sm'} animation="border" variant="warning"/>
                    <span className={'text-primary'}>Buffering</span>
                </div>
            default:
                return <span>Get status</span>
        }
    }

    return (

        <div className={'card mb-3 p-1'}>
            <Row className='align-items-center px-2'>

                <Col sm={1} className={'ml-2 me-2 text-center'}>
                    <StateIcon />
                    {/*<span className='bx bx-tv'/>*/}
                </Col>
                <Col className={'align-items-center d-flex flex-column'}>
                    <span >{channel.id}</span>
                    <span className={'small text-nowrap text-warning'}>{channel.name}</span>
                </Col>
                <Col>
                    <span className='bx bx-world me-1 mb-1'/>
                    {channel.provider.name}
                </Col>
                <Col className={'text-nowrap'}>
                    <span className={'bx bx-time me-1 mb-1'}/>
                    <span>{state?.status.time || '00:00:00'}</span>
                </Col>

                <Col>
                    <small>
                        <div className={'d-flex flex-column align-items-center'}>

                            <div>
                                <span className={'bx bxs-playlist me-1 mb-1'}/>
                                {channel.provider.format}
                            </div>
                            <div>
                                <span className={'bx bx-cog me-1 mb-1'}/>
                                {capitalizeFirstLetter(channel.provider.engine)}
                            </div>
                        </div>
                    </small>

                </Col>
                {/*<Col sm={1} style={{fontSize: '1.8rem'}}>*/}
                {/*    {channel.lang && <ReactCountryFlag countryCode={langToCountryCode(channel.lang)}/>}*/}
                {/*</Col>*/}
                <Col style={{whiteSpace: 'nowrap'}}>
                    <small>
                        <span className={'bx bx-link me-1 mb-1'}/>
                        {channel.provider.format === 'HLS' ? `http://HOST:PORT/${channel.id}/hls/playlist.m3u8` : `http://127.0.0.1:8989/${channel.id}/ts`}
                    </small>
                </Col>
                <Col>
                    <small>
                        <div className={'d-flex flex-column'}>
                            <span className={'text-nowrap'}>FPS: {state?.status.fps || '0'}</span>
                            <span className={'text-nowrap'}>Frame: {state?.status.frame || '0'}</span>
                        </div>
                    </small>
                </Col>
                <Col className={'flex-grow-1 text-end'}>
                    <ButtonGroup>
                        {channel.provider.format === 'HLS' &&
                            <Button variant='primary' disabled={state ? state?.name !== "Online" : true}
                                    onClick={() => watch(channel.id, channel.name)}><span
                                className='bx bx-movie-play'></span>Watch</Button>}
                        <Button variant='danger' onClick={() => stop(channel.id)}><span className='bx bx-stop'></span>Stop</Button>
                    </ButtonGroup>
                </Col>

            </Row>
            <Row>
                <Col>

                </Col>
            </Row>
        </div>

    );
};

export default OnlineChannelItem;
